body {
    background-color: #274973;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
    font-family: Ubuntu, serif;
}

.btn {
    border: 0;
    outline: 0;
    height: 50px;
    cursor: pointer;
}

.App {
    width: 600px;
    min-height: 200px;
    background-color: #85a3bb;
    border-radius: 10px;
    box-shadow: 0 0 50px #26405a;
    padding: 10px 20px;
}

.app__title {
    text-align: center;
    margin: 0 0 30px 0;
}

.form {
    display: flex;
}

.form__input {
    height: 50px;
    width: 67%;
    outline: none;
    padding: 0 10px;
    border: 2px solid #26405a;
    border-bottom: none;
    color: #26405a;
    border-radius: 2px 0 0 0;
    transition: .4s ease background-color, .4s ease color;
}
.form__input::placeholder {color: #26405a;}

.form__input:focus {
    background-color: #63acc7;
    color: black;
}

.form__input:focus::placeholder {opacity: 1;}

.form__button {
    width: 33%;
    background-color: #197f4f;
    border: 2px solid #26405a;
    border-bottom: none;
    border-left: none;
    border-radius: 0 2px 0 0;
    transition: .4s ease background-color , .4s ease color;
}

.form__button:hover {
    background-color: #0d4d2f;
    color: white;
}

.todo__list {
    list-style: none;
    max-height: 650px;
    overflow: hidden;
    overflow-y: auto;
}


.todo__item {
    height: 50px;
    display: flex;
    align-items: center;
    padding: 0 10px;
    border: 2px solid #26405a;
    border-bottom: none;
    gap: 10px;
    transition: .4s ease background-color;
}

.todo__item:hover {background-color: #698499;}
.todo__item:last-child {border-bottom: 2px solid #26405a;}
.todo__item:nth-child(even) {background-color: #add7e5;}
.todo__item:nth-child(even):hover {background-color: #63acc7;}

.todo__item label {
    display: flex;
    cursor: pointer;
    align-items: center;
    gap: 10px;
    width: 100%;
    height: 100%;
}
.todo__item label input {display: none}
.todo__item label b {font-weight: 400}

.todo__item label span {
    border: 2px solid #197f4f;
    width: 20px;
    height: 20px;
    border-radius: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
}
.todo__item label input:checked ~ span {background-color: #197f4f;}
.todo__item label input:checked ~ b {text-decoration: line-through;}

.todo__item label input:checked ~ span::after {
    content: '';
    width: 10px;
    height: 10px;
    background-color: black;
    border-radius: 50%;
}

.todo__item_btn {
    color: white;
    margin-left: auto;
    background-color: #901e34;
    width: 20px;
    height: 20px;
    border-radius: 2px;
    display: flex;
    justify-content: center;
    align-items: center;
    cursor: pointer;
}

.todo__footer {
    display: flex;
}

.todo__footer_completed {
    border: 2px solid #26405a;
    border-top: none;
    border-right: none;
    border-radius: 0 0 2px 0;
    width: 33%;
    display: flex;
    align-items: center;
    justify-content: center;
}

.todo__clearAll {
    background-color: #901e34;
    border: 2px solid #26405a;
    border-top: none;
    border-left: none;
    border-radius: 0 0 0 2px;
    transition: background-color ease .4s, color ease .4s;
    width: 33%;
}

.todo__clearAll:hover {
    background-color: #5f0e1e;
    color: white;
}

.todo__clearCompleted {
    background-color: #ee9454;
    border: 2px solid #26405a;
    border-top: none;
    border-radius: 0 0 2px 0;
    transition: background-color ease .4s, color ease .4s;
    width: 34%;
}

.todo__clearCompleted:hover {
    background-color: #ce6921;
    color: white;
}


@media only screen and (max-width: 1440px) {.todo__list {max-height: 550px;}}
@media only screen and (max-width: 600px) {
    .App {width: 500px; padding: 10px; min-height: 100px;}
    .app__title {margin: 0 0 10px 0;font-size: 24px}
}
@media only screen and (max-width: 500px) {.App {width: 400px;}}
@media only screen and (max-width: 400px) {
    .App {width: 340px;}
    .form__input{font-size: 14px}
    .form__button {font-size: 14px}
    .todo__item b {font-size: 14px}
    .todo__clearCompleted {font-size: 14px; letter-spacing: -0.4px}
    .todo__clearAll {font-size: 14px;}
    .todo__footer_completed {font-size: 14px; letter-spacing: -0.1px}
}
